/* Basic reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body styling */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f0f0f0; /* Light gray background */
  color: #333; /* Dark text for contrast */
}

/* App container */
.App {
  max-width: 600px; /* Max width for the app */
  margin: 50px auto; /* Center the app */
  padding: 20px;
  background-color: #fff; /* White background for the app */
  border-radius: 8px; /* Rounded corners */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
}

/* Title styling */
h1 {
  text-align: center; /* Center the title */
  color: #4CAF50; /* Green color for the title */
  margin-bottom: 20px; /* Space below the title */
}

/* Input field styling */
input[type="text"] {
  width: 100%; /* Full width */
  padding: 10px; /* Padding for input */
  margin-bottom: 20px; /* Space below the input */
  border: 1px solid #ccc; /* Light gray border */
  border-radius: 4px; /* Rounded corners */
  font-size: 16px; /* Font size */
}

/* Button styling */
button {
  width: 100%; /* Full width */
  padding: 10px; /* Padding for button */
  background-color: #4CAF50; /* Green background */
  color: white; /* White text */
  border: none; /* No border */
  border-radius: 4px; /* Rounded corners */
  font-size: 16px; /* Font size */
  cursor: pointer; /* Pointer cursor */
  transition: background-color 0.3s; /* Smooth transition for hover */
}

/* Button hover effect */
button:hover {
  background-color: #45a049; /* Darker green on hover */
}

/* Translation output styling */
p {
  font-size: 18px; /* Larger font for output */
  margin-top: 20px; /* Space above output */
  text-align: center; /* Center the output text */
  color: #333; /* Dark text color */
}
